.hero {
  min-height: 100vh;
  padding: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;

  background: linear-gradient(135deg, #141e30, #243b55);
  background-size: 200% 200%;
  animation: slowGradient 12s ease infinite;
}

.hero-content {
  text-align: center;
  color: white;
  max-width: 600px;

  animation: slideScale 1.1s ease-out forwards;
}

.hero-title {
  font-size: clamp(2rem, 5vw, 3.2rem);
  margin-bottom: 1rem;
  animation: floatText 4s ease-in-out infinite;
}

.hero-title span {
  color: #4fd1c5;
}

.hero-subtitle {
  font-size: clamp(1rem, 3.5vw, 1.2rem);
  line-height: 1.6;
  margin-bottom: 2rem;
  opacity: 0.85;
}

/* Explore button pulse animation */
.hero-btn {
  padding: 0.85rem 2.2rem;
  font-size: 1rem;
  border-radius: 30px;
  border: none;
  cursor: pointer;

  background: #4fd1c5;
  color: #0f172a;
  font-weight: 600;

  animation: pulse 2s infinite;
  transition: transform 0.25s ease;
}

.hero-btn:hover {
  transform: scale(1.08);
}

/* ðŸ”¹ Animations */

@keyframes slideScale {
  from {
    opacity: 0;
    transform: translateY(40px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

@keyframes floatText {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-8px);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(79, 209, 197, 0.7);
  }
  70% {
    box-shadow: 0 0 0 18px rgba(79, 209, 197, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(79, 209, 197, 0);
  }
}

@keyframes slowGradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* ðŸ“± Mobile */
@media (max-width: 480px) {
  .hero-btn {
    width: 100%;
  }
}
